function AccordionEstadoCtrl($scope, statesService) {
  'ngInject';
  // ViewModel

  var vm = this;

  vm.empreedimentos = [];
  vm.states = [];
  vm.cities = [];
  vm.empId = {};
  vm.empNome = {};
  vm.loadingStates = true;

  // statesService.getDados().then(function (res) {
  //   vm.loadingStates = false;
  //   $scope.$apply(function () {
  //     vm.states = statesService.listarEstados(res.Empreendimentos);
  //     vm.empreedimentos = res.Empreendimentos;
  //   });
  // });

  statesService.getDadosAsync().then(function (res) {
    vm.loadingStates = false;
    if ($scope.$root.$$phase != '$apply' && $scope.$root.$$phase != '$digest') {
      $scope.$apply(function () {
        vm.states = statesService.listarEstados(res.Empreendimentos);
        vm.empreedimentos = res.Empreendimentos;
      });
    } else {
      vm.states = statesService.listarEstados(res.Empreendimentos);
      vm.empreedimentos = res.Empreendimentos;
    }
  });

  vm.getCities = function (state) {
    vm.cities = statesService.listarCidades(vm.empreedimentos, state);
  };

  vm.getEmpreendimento = function (state, city) {
    var empreedimento = statesService.obterEmpreendimento(vm.empreedimentos, state, city);

    vm.empId = empreedimento.EmpId;
    vm.empNome = empreedimento.NomeEmpreendimento;
  };

  ////
}

exports.default = {
  name: 'AccordionEstadoCtrl',
  fn: AccordionEstadoCtrl
};

