function MenuCtrl($http, $state, $timeout, AppSettings, searchData, $rootScope) {
  'ngInject';
  // ViewModel

  var vm = this;

  $rootScope.showload = true;

  var options = {
    enableHighAccuracy: true,
    timeout: 5000,
    maximumAge: 0
  };

  function success(position) {
    var lat = position.coords.latitude;
    var lng = position.coords.longitude;
    // console.log("Current position: " + lat + " " + lng);

    $http.get('http://maps.google.com/maps/api/geocode/json?address=' + lat + ',' + lng + '&sensor=false').then(function (data) {
      console.log(data.data.results[0].address_components, 'success get google');
      data.data.results[0].address_components.map(function (item, i) {
        if (item.short_name.length == 2 && item.types[0] == 'administrative_area_level_1' && item.types[1] == 'political') {
          vm.defaultState = item.short_name;
          vm.defaultStateLong = item.long_name;
          localStorage.setItem('myUf', item.short_name);
          localStorage.setItem('myUfLong', item.long_name);
        }
      });
    }, function (err) {
      console.log(err, 'error get google');
    });

    localStorage.setItem('myLat', lat);
    localStorage.setItem('myLong', lng);
  }

  function error() {
    console.log('error');
    $http({
      url: AppSettings.states.current.endpoint,
      method: 'GET'
    }).then(function (data) {
      vm.defaultState = data.data.uf;
      vm.defaultStateLong = data.data.title;
      localStorage.setItem('myLat', data.data.latitude);
      localStorage.setItem('myLong', data.data.longitude);
      localStorage.setItem('myUf', data.data.uf);
      localStorage.setItem('myUfLong', data.data.title);
    }, function (err) {
      console.log(err);
    });
  }

  // navigator.geolocation.getCurrentPosition(success, error, options);

  // $http({
  //   url: 'https://ipinfo.io/geo',
  //   method: 'GET'
  // })
  //   .then(data => {
  //       console.log( data )
  //     }, err => {
  //       console.log(err);
  //     })

  if (navigator.geolocation && localStorage.getItem('myLat') === null) navigator.geolocation.getCurrentPosition(success, error, options);else {
    vm.defaultState = localStorage.getItem('myUf');
    vm.defaultStateLong = localStorage.getItem('myUfLong');
  }

  $timeout(function () {
    $http({
      url: AppSettings.states.list.endpoint,
      method: 'GET'
    }).then(function (data) {
      console.log(data, 'list');
      vm.states = data.data;

      vm.states.map(function (item, i) {
        item.current = item.uf === localStorage.getItem('myUf');
      });
    }, function (err) {
      console.log(err);
    });
  }, 5000);

  vm.init = function () {
    vm.navigation = true;
    vm.buscaSection = false;
    vm.openMenuForce = false;
    vm.stateMenu = false;
    vm.menuBack = false;
  };
  vm.init();

  vm.navigation = false;

  vm.back = function () {
    // console.log('back!', vm.navigation, vmSection, vm.stateMenu)
    if (vm.navigation == false && (vm.buscaSection == true || vm.stateMenu == true)) {
      vm.navigation = true;
      vm.buscaSection = false;
      vm.openMenuForce = false;
      vm.menuBack = false;
    }
  };

  vm.searchResult = function () {
    vm.navigation = false;
    vm.openMenuForce = false;
    vm.stateMenu = false;

    vm.menuBack = true;
    vm.buscaSection = true;
  };
  vm.openBusca = function () {
    vm.navigation = false;
    vm.stateMenu = false;

    vm.openMenuForce = true;
    vm.buscaSection = true;
    vm.menuBack = true;
  };

  vm.openState = function () {
    vm.navigation = false;
    vm.buscaSection = false;

    vm.openMenuForce = true;
    vm.stateMenu = true;
    vm.menuBack = true;
  };
  vm.setFocus = function () {
    setTimeout(function () {
      $('menu-directive [ng-model="menu.busca"]:visible').focus();
    }, 150);
  };
  vm.changeUf = function (item) {
    if (item.status == 0) {
      return false;
    }

    for (var i = 0; i < vm.states.length; i++) {
      vm.states[i].current = false;
    }

    item.current = true;
    vm.defaultState = item.uf;
    vm.defaultStateLong = item.title;

    localStorage.setItem('myLat', item.latitude);
    localStorage.setItem('myLong', item.longitude);
    localStorage.setItem('myUf', item.uf);
    localStorage.setItem('myUfLong', item.title);
    vm.init();

    $state.reload();
  };

  vm.filterUf = function (data) {
    return data.uf.uf != vm.defaultState;
  };

  vm.clickSearch = function () {
    $state.go('Busca', { term: vm.busca });
  };

  vm.clickHome = function () {
    window.location = '/home';
  };

  //logica auto complete
  $timeout(function () {
    searchData.getDados('', '').then(function (data) {
      console.log(data.data, 'autocomplete');
      vm.searchMenu = data.data;
      vm.searchMenu.url = AppSettings.apiRootUrl;
    });
  }, 5000);

  $('.navbar-toggler-icon').on('click', function () {
    $('body').toggleClass('menuopen');
  });

  $rootScope.$on('pageLoaded', function (e) {
    vm.init();
  });
}

exports.default = {
  name: 'MenuCtrl',
  fn: MenuCtrl
};

