function AccordionLocalizacaoDirective($timeout) {
  'ngInject';

  return {
    restrict: 'EA',
    templateUrl: 'directives/accordion-localizacao.html',
    controller: 'AccordionLocalizacaoCtrl',
    controllerAs: 'accordionLocalizacao',
    scope: {
      prmConteudo: '=prmConteudo'
    },
    link: function link(scope, elem) {
      function reloadMap() {
        $timeout(function () {
          $(elem).find('.area-mapa-local').each(function (i, item) {
            console.log('map', $(item).is(':visible'));
            if (!$(item).is(':visible')) {
              return false;
            }

            var mapData = {
              lat: scope.prmConteudo.empreendimento.std_latitude * 1,
              lng: scope.prmConteudo.empreendimento.std_longitude * 1
            };
            var map = new google.maps.Map(item, {
              center: mapData,
              zoom: 16
            });
            var marker = new google.maps.Marker({
              position: mapData,
              map: map,
              title: scope.prmConteudo.empreendimento.name
            });
            var infowindow = new google.maps.InfoWindow({
              content: '<p>' + scope.prmConteudo.empreendimento.name + '<br>' + '<strong>' + scope.prmConteudo.empreendimento.std_sub_title + '</strong></p>'
            });
            marker.addListener('click', function () {
              infowindow.open(map, marker);
            });
          });
        }, 200);
      }

      reloadMap();

      scope.reloadMap = reloadMap;
    }
  };
}

exports.default = {
  name: 'accordionLocalizacaoDirective',
  fn: AccordionLocalizacaoDirective
};

