function ActionRulerDirective($timeout) {
  'ngInject';

  return {
    restrict: 'EA',
    templateUrl: 'directives/action-ruler.html',
    controller: 'ActionRulerCtrl',
    controllerAs: 'ruler',
    scope: {
      prmPage: '=prmPage',
      prmMobile: '=prmMobile',
      prmDesktop: '=prmDesktop',
      prmConteudo: '=prmConteudo',
      prmCustomTitle: '=?'
    },
    link: function link(scope, elem) {
      var $w = $(window),
          top = 570;

      $w.on("scroll", function () {
        if ($w.scrollTop() >= top) {
          $timeout(function () {

            scope.fixed = true;

            status = 'colado';
          });
        } else {
          $timeout(function () {

            scope.fixed = false;

            status = 'layout';
          });
        }
      });

      $timeout(function () {
        var $itens = $('.action-ruler .container .container-action-ruler .item:not(.first-item)'),
            qtd = $itens.length,
            div = 0;

        if (qtd < 5) {
          div = 100 / qtd;

          $itens.css('width', div + '%');
        }
      });

      $(window).keyup(function (event) {
        if (event.keyCode == 27 && ($(elem).find('.area-modal-chat').is('visible') || $(elem).find('.area-modal-email').is(':visible') || $(elem).find('.area-modal-whats').is(':visible'))) {
          $(elem).find('.btn-reset-modal').click();
        }
      });

      $(elem).find('*').keyup(function (event) {
        if (event.keyCode == 27 && ($(elem).find('.area-modal-chat').is(':visible') || $(elem).find('.area-modal-email').is(':visible') || $(elem).find('.area-modal-whats').is(':visible'))) {
          $(elem).find('.btn-reset-modal').click();
        }
      });

      $timeout(function () {
        var location = window.location.href,
            facebook = $('.facebook a'),
            twitter = $('.twitter a'),
            emailbox = $('#emailbox a');

        facebook.attr('href', facebook.attr('href') + location);
        twitter.attr('href', twitter.attr('href') + location);
        emailbox.attr('href', emailbox.attr('href') + location);
      });

      $timeout(function () {
        $(elem).find('.item.stand').click(function () {
          $('html, body').scrollTop($('#ref-stand').offset().top - 150);
        });
      });
    }
  };
}

exports.default = {
  name: 'actionRulerDirective',
  fn: ActionRulerDirective
};

