'use strict';

AreasVerdesDirective.$inject = ["$timeout"];
Object.defineProperty(exports, "__esModule", {
  value: true
});
function AreasVerdesDirective($timeout) {
  'ngInject';

  return {
    restrict: 'EA',
    templateUrl: 'directives/areas-verdes.html',
    controller: 'AreasVerdesCtrl',
    controllerAs: 'areasVerdes',
    scope: {
      prmAreaCurrentItem: '=',
      // prmAreaCurrentItemTexto: '=?',
      prmAreaVerticalSlider: '=',
      prmAreaItens: '=',
      prmAreaConteudo: '=prmAreaConteudo'
    },
    link: function link(scope, elem, attr, ctrl) {
      var owl = $(elem).find('.slider-areas-verdes'),
          pos = 0,
          auxPosUp = true,
          auxPosDown = false,
          $obj = $(elem).find('.scroll'),
          maxScroll = scope.prmAreaItens.length * 90 - 450;

      owl.on('changed.owl.carousel', function (event) {
        $timeout(function () {
          scope.$apply(function () {
            if (event.item.index === null) return false;

            scope.prmAreaCurrentItem = event.item.index + 1 < 10 ? '0' + (event.item.index + 1) : event.item.index + 1;
            scope.prmAreaCurrentItemTexto = scope.prmAreaItens[event.item.index].title;
            disable();
            scope.prmAreaItens[event.item.index].active = true;
            console.log('areas verdes');
          });
        });
      });
      owl.on('initialized.owl.carousel', function (event) {
        $timeout(function () {
          scope.$apply(function () {
            scope.prmAreaCurrentItem = event.item.index + 1 < 10 ? '0' + (event.item.index + 1) : event.item.index + 1;
            scope.prmAreaCurrentItemTexto = scope.prmAreaItens[event.item.index].title;
            disable();
            scope.prmAreaItens[event.item.index].active = true;
          });
        });
      });
      scope.goTo = function (i) {
        // owl.trigger('to.owl.carousel', [i, 500]);
        owl.trigger('to.owl.carousel', [i, 0]);
        disable();
        scope.prmAreaItens[i].active = true;
      };
      $timeout(function () {
        scope.$apply(function () {
          scope.buttonUp = true;
          scope.buttonDown = false;
        });
        $obj = $(elem).find('.scroll');
        $obj.on('scroll', function () {
          pos = $(this).scrollTop();
          btnAtv();
        });
      });
      function btnAtv() {
        auxPosUp = pos - 90 <= 0;
        auxPosDown = pos + 90 >= maxScroll;

        $timeout(function () {
          scope.$apply(function () {
            scope.buttonUp = auxPosUp;
            scope.buttonDown = auxPosDown;
          });
        });
      }
      function disable() {
        scope.prmAreaItens.map(function (item, i) {
          item.active = false;
          console.log(i, item);
        });
      }
      scope.goUp = function () {
        pos = $obj.scrollTop();
        $obj.animate({ scrollTop: pos - 90 });
        btnAtv();
      };
      scope.goDown = function () {
        pos = $obj.scrollTop();
        $obj.animate({ scrollTop: pos + 90 });
        btnAtv();
      };
    }
  };
}

exports.default = {
  name: 'areasVerdesDirective',
  fn: AreasVerdesDirective
};

