function EmpreendimentoSliderDirective($timeout) {
    'ngInject';

    return {
        restrict: 'EA',
        templateUrl: 'directives/empreendimento-slider.html',
        controller: 'EmpreendimentoSliderCtrl',
        controllerAs: 'empreendimentoSlider',
        scope: {
            prmCurrentItem: '=',
            prmVerticalSlider: '=',
            prmItens: '=',
            prmConteudo: '=prmConteudo',
            prmTitle: '=prmTitle',
            prmMidias: '=prmMidias'
        },
        link: function link(scope, elem, attr, ctrl) {
            var owl = $(elem).find('.slider'),
                pos = 0,
                auxPosUp = true,
                auxPosDown = false,
                $obj = $(elem).find('.scroll'),
                maxScroll = scope.prmItens.length * 90 - 450;

            owl.on('changed.owl.carousel', function (event) {
                $timeout(function () {
                    scope.$apply(function () {
                        if (event.item.index === null) return false;

                        scope.prmCurrentItem = event.item.index + 1 < 10 ? '0' + (event.item.index + 1) : event.item.index + 1;
                        scope.prmCurrentItemText = scope.prmItens[event.item.index].image_description;
                        disable();
                        scope.prmItens[event.item.index].active = true;
                    });
                });
            });
            owl.on('initialized.owl.carousel', function (event) {
                $timeout(function () {
                    scope.$apply(function () {
                        scope.prmCurrentItem = event.item.index + 1 < 10 ? '0' + (event.item.index + 1) : event.item.index + 1;
                        scope.prmCurrentItemText = scope.prmItens[event.item.index].image_description;
                        disable();
                        scope.prmItens[event.item.index].active = true;
                    });
                });
            });
            scope.goTo = function (i) {
                // (Vitor Pereira - Remover Efeito) 
                // owl.trigger('to.owl.carousel', [i, 500]);
                owl.trigger('to.owl.carousel', [i, 0]);
                disable();
                scope.prmItens[i].active = true;
            };
            $timeout(function () {
                scope.$apply(function () {
                    scope.buttonUp = true;
                    scope.buttonDown = false;
                });
                $obj = $(elem).find('.scroll');
                $obj.on('scroll', function () {
                    pos = $(this).scrollTop();
                    btnAtv();
                });
            });
            function btnAtv() {
                auxPosUp = pos - 90 <= 0;
                auxPosDown = pos + 90 >= maxScroll;
                $timeout(function () {
                    scope.$apply(function () {
                        scope.buttonUp = auxPosUp;
                        scope.buttonDown = auxPosDown;
                    });
                });
            }
            function disable() {
                scope.prmItens.map(function (item, i) {
                    item.active = false;
                    //console.log(i, item);
                });
            }
            scope.goUp = function () {
                pos = $obj.scrollTop();
                $obj.animate({ scrollTop: pos - 90 });
                btnAtv();
            };
            scope.goDown = function () {
                pos = $obj.scrollTop();
                $obj.animate({ scrollTop: pos + 90 });
                btnAtv();
            };

            $timeout(function () {
                // $(elem).find('.empreendimento-lightbox-target img').remove();
                // $(elem).find('.empreendimento-lightbox-target').append('<img />');

                $(elem).find('.zoomexclusivo').click(function () {
                    var img = $(this).data('image');
                    $('.empreendimento-lightbox-target img').attr('src', img);
                });
            });

            $(window).keyup(function (event) {
                if (event.keyCode == 27 && $(elem).find('.content-modal').css('opacity') > 0) {
                    $(elem).find('[href="#zoom-close"]').click();
                    console.log('empreendimentoSlider');
                }
            });

            $(elem).find('*').keyup(function (event) {
                if (event.keyCode == 27 && $(elem).find('.content-modal').css('opacity') > 0) {
                    $(elem).find('[href="#zoom-close"]').click();
                    console.log('empreendimentoSlider');
                }
            });
        }
    };
}

exports.default = {
    name: 'empreendimentoSliderDirective',
    fn: EmpreendimentoSliderDirective
};

