OnRun.$inject = ["$rootScope", "AppSettings"];
Object.defineProperty(exports, "__esModule", {
  value: true
});
function OnRun($rootScope, AppSettings) {
  'ngInject';

  $rootScope.$on('$stateChangeStart', function (event, next, current) {
    $rootScope.loadingRoute = true;
    $rootScope.pageTitle = '';
    $rootScope.pageDescription = '';
  });

  // change page title based on state
  $rootScope.$on('$stateChangeSuccess', function (event, toState) {
    if ($rootScope.empreendimentoTitle) {
      $rootScope.pageTitle += $rootScope.empreendimentoTitle;
    } else if (toState.title && !$rootScope.pageTitle) {
      $rootScope.pageTitle += toState.title;
    }

    $rootScope.loadingRoute = false;

    $rootScope.pageTitle += ' \u2014 ' + AppSettings.appTitle;

    //removemenu
    $('body').removeClass('menuopen');
    $rootScope.$broadcast('pageLoaded');
    // $(window).scrollTop(0)
  });
}

exports.default = OnRun;

