function searchData($http, AppSettings) {
    'ngInject';

    var service = {};

    service.getDados = function () {
        var term = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '';
        var where = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : '';

        var lat = localStorage.getItem('myLat') == null ? '-23.5400516' : localStorage.getItem('myLat'),
            lng = localStorage.getItem('myLong') == null ? '-46.3834606' : localStorage.getItem('myLong');

        return $http.get(AppSettings.home.banner.endpoint + '?latitude=' + lat + '&longitude=' + lng + '&search=' + term + '&orderby=status_obra ASC' + '&where=' + where);
    };

    return service;
}

exports.default = {
    name: 'searchData',
    fn: searchData
};

