function construtorVendasService($http, AppSettings, AppUtils) {
    'ngInject';

    var service = {};

    service.sendApiCv = function (name, email, ddd, phone, dddAdicional, celularAdicional, idCv, descricaoCv, tipo, praca, idEmpreendimento) {

        var queryString = window.location.search;
        var urlParams = new URLSearchParams(queryString);

        var cookieUtmCampaign = AppUtils.getCookie('utm_campaign');
        var cookieUtmMedium = AppUtils.getCookie('utm_medium');
        var cookieUtmSource = AppUtils.getCookie('utm_source');
        var cookieUtmTerm = AppUtils.getCookie('utm_term');
        var cookieUtmContent = AppUtils.getCookie('utm_content');

        var Utm_campaign = urlParams.get('utm_campaign') || cookieUtmCampaign;
        var Utm_medium = urlParams.get('utm_medium') || cookieUtmMedium;
        var Utm_source = urlParams.get('utm_source') || cookieUtmSource;
        var Utm_term = urlParams.get('utm_term') || cookieUtmTerm;
        var Utm_content = urlParams.get('utm_content') || cookieUtmContent;

        if (cookieUtmCampaign) {
            AppUtils.setCookieByminutes('utm_campaign', cookieUtmCampaign, 30);
        }
        if (cookieUtmMedium) {
            AppUtils.setCookieByminutes('utm_medium', cookieUtmMedium, 30);
        }
        if (cookieUtmSource) {
            AppUtils.setCookieByminutes('utm_source', cookieUtmSource, 30);
        }
        if (cookieUtmTerm) {
            AppUtils.setCookieByminutes('utm_term', cookieUtmTerm, 30);
        }
        if (cookieUtmContent) {
            AppUtils.setCookieByminutes('utm_content', cookieUtmContent, 30);
        }

        var traffic_source = Utm_source;
        var c_utmz = AppUtils.getCookie('__utmz');
        var rdtrk = AppUtils.getCookie('_rdtrk');

        traffic_source = traffic_source || 'direct';
        //Utm_medium = Utm_medium || 'referral';

        var traffic_medium = Utm_medium || '(none)'; //'CPC' = busca paga
        var traffic_campaign = Utm_campaign;
        var traffic_value = Utm_term;
        var traffic_content = Utm_content;

        var data = {
            "name": name,
            "email": email,
            "ddd": ddd,
            "celular": phone,
            "ddd_adicional": dddAdicional,
            "celular_adicional": celularAdicional,

            "id_empreendimento": idCv,

            "id_emp": idEmpreendimento,
            "id_cv": idCv,
            "descricao_cv": descricaoCv,
            "tipo": tipo,
            "praca": praca,

            "origin_utm_source": traffic_source,
            "origin_utm_medium": traffic_medium,
            "origin_utm_campaign": traffic_campaign,
            "origin_utm_value": traffic_value,
            "origin_utm_content": traffic_content
        };

        return new Promise(function (resolve) {
            $http.post(AppSettings.construtorVendas.send.endpoint, data).then(function (res) {
                var returnData = {
                    success: res.status == 200,
                    statusText: res.statusText
                };
                resolve(returnData);
            }, function (err) {
                console.log('retorno CV', err);
                var returnData = {
                    success: false,
                    statusText: 'Erro ao receber retorno do envio.',
                    error: err
                };
                resolve(returnData);
            });
        });
    };

    return service;
}

exports.default = {
    name: 'construtorVendasService',
    fn: construtorVendasService
};

